//index.js
// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './index.vue'
import router from './router'
import ElementUI from 'element-ui';
import axios from 'axios'
import VueAxios from 'vue-axios'
// import { Pagination, Select,Menu  } from 'element-ui';
import 'babel-polyfill';
import 'element-ui/lib/theme-chalk/index.css';
import 'font-awesome/css/font-awesome.min.css'
import 'vue2-animate/dist/vue2-animate.min.css'
import 'animate.css'
import echarts from 'echarts'
import '@/components/report/report.css'

Vue.use(ElementUI);
Vue.use(VueAxios, axios)
Vue.prototype.echarts = echarts;

Vue.config.productionTip = false

if((process.env.NODE_ENV === 'production')){
  // build下运行
  var pathName = document.location.href;
  var index = pathName.substr(1).indexOf("/index");
  var result = pathName.substr(0, index + 1);
  Vue.prototype.HOST = result;
}else{
//本地开发运行：
Vue.prototype.HOST = '/api'
}

Vue.prototype.tId = null;

Vue.prototype.throttle = function (method, context) {
  clearTimeout(this.tId);
  this.tId = setTimeout(function () {
    method.call(context);
  }, 200);
};

/* eslint-disable no-new */
var vue = new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  data: {
    eventHub: new Vue()//新建一个空的vue对象
  }
})
export default vue



