export function getMemberList(obj) {
  let host = obj ? obj.HOST : context.HOST
  const url = '/user/getMemberList'
  return axios({
    method: "get",
    url: host + url,
  }).then((res) => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

//协会审批
export function approvalMember(data) {
  const url = context.HOST + '/user/judgeMember'
  let params = Qs.stringify(data);
  return axios({
    method: "post",
    url: url,
    data: params
  }).then(res => {
    return Promise.resolve(res.data)
  }).catch((err) => {
    return Promise.reject(err.data)
  })
}

//查看用户已订阅
export function getSubscribeList() {
  const url = context.HOST + '/user/getSubscribeList'
  return axios({
    method: 'get',
    url: url
  }).then(res => Promise.resolve(res.data)).catch(err => Promise.reject(err.data))
}

//保存用户订阅
export function addSubscribes(ids) {
  const url = context.HOST + '/user/addSubscribes'
  let params = Qs.stringify({
    categoryIds: ids.toString()
  })
  return axios({
    method: 'post',
    url: url,
    data: params
  }).then(res => Promise.resolve(res.data)).catch(err => Promise.reject(err.data))
}

//获取订阅内容
export function getSubscribe(prefix) {
  const url = prefix + '/information/getSubscribe'
  return axios({
    method: 'get',
    url: url
  }).then(res => Promise.resolve(res.data)).catch(err => Promise.reject(err.data))
}



