<!--
 * @Description:文章评论页
 * @Author: your name
 * @Date: 2019-07-09 16:02:39
 * @LastEditTime: 2019-07-18 20:14:07
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div id="comment">
    <div id="statement">声明： 本文由入驻公众服务平台的作者攒写，观点仅代表作者本人，不代表平台立场。如果有侵权或其他问题，请联系举报。</div>
    <div class="comment_top">
      <div class="comment_outline">
        <div>
          <h4>我来说两句</h4>
          <div class="comment_data">
            （共
            <a>
              <strong>0</strong>
            </a>条评论，
            <a>
              <strong>0</strong>
            </a>
            人参与）
          </div>
        </div>
        <div class="acount_right">
          <div class="comment_account">（当前用户：{{userName}}）</div>
        </div>
      </div>
      <textarea placeholder="文明社会，理性评论" v-model="value"></textarea>
      <button class="sendcomment" @click="reply(value),value=''" src="#">发布评论</button>
    </div>
    <div class="comment_bottom">
      <div class="comment_show_top">
        <ul class="comment_classify_btn">
          <li>
            <a @click="handleclick1" :class="[{'title-active':isOn1}]">最新评论</a>
          </li>
          <li>
            <a @click="handleclick2" v-if="0" :class="[{'title-active':isOn2}]">热门评论</a>
          </li>
        </ul>
      </div>
      <div v-if="1" class="comment_show_down">
        <div class="aComment" v-for="(i,index) in list">
          <span style="color:black;display:inline-block;margin-bottom:5px">{{i.user.username}}：</span>
          <div
            style="float:right;cursor:pointer;color:#127de7"
            @click="i.showComment=!i.showComment"
          >回复</div>
          <br />
          {{i.comments}}
          <p
            v-if="!i.showSons&&!i.sons.length&&i.replyCount>0"
            @click="addSons(index)"
            style="margin:3px 0;cursor:pointer;color:#127de7"
          >查看回复</p>
          <div v-if="i.showComment" style="white-space:nowrap">
            <el-input style="margin:5px 0;width:90%" v-model="i.reComment" placeholder="请输入内容"></el-input>
            <el-button
              type="primary"
              style="width:calc(10% - 5px);margin-left:5px"
              @click="reComment(i.reComment,i.id),i.reComment=''"
            >确定</el-button>
          </div>
          <div v-for="j in i.sons" style="padding:3px 0;padding-left:1rem">
            <span style="color:black">{{j.user.username}}：</span>
            {{j.comments}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isOn1: true,
      isOn2: false,
      userName: global.userItem.userName,
      value: "",
      list: [
        {
          id: 2,
          comments: "123",
          time: "2020-01-03 22:31",
          informationId: 1,
          showComment: 0,
          showSons: 0,
          sons: [
            {
              id: 3,
              comments: "回复",
              time: "2020-01-03 22:09",
              user: {
                id: 1,
                username: "moster",
                password: null,
                phone: null,
                email: null,
                permissions: null,
                role: null,
                status: null
              },
              commentId: 1,
              fromReplyId: 1, //被回复的回复的id（回复评论的回复此id为0）
              file: null
            },
            {
              id: 1,
              comments: "回复",
              time: "2020-01-03 22:05",
              user: {
                id: 8,
                username: "丁汉",
                password: null,
                phone: null,
                email: null,
                permissions: null,
                role: null,
                status: null
              },
              commentId: 1,
              fromReplyId: 0,
              file: null
            },
            {
              id: 2,
              comments: "回复",
              time: "2020-01-03 22:05",
              user: {
                id: 1,
                username: "moster",
                password: null,
                phone: null,
                email: null,
                permissions: null,
                role: null,
                status: null
              },
              commentId: 1,
              fromReplyId: 1,
              file: null
            }
          ],
          user: {
            id: 1,
            username: "moster",
            password: null,
            phone: null,
            email: null,
            permissions: null,
            role: null,
            status: null
          },
          replyCount: 0,
          file: null
        },
        {
          id: 1,
          comments: "123",
          time: "2020-01-03 21:54",
          showComment: 0,
          sons: [],
          showSons: 0,
          informationId: 1,
          user: {
            id: 3,
            username: "qqq",
            password: null,
            phone: null,
            email: null,
            permissions: null,
            role: null,
            status: null
          },
          replyCount: 3,
          file: null
        }
      ]
    };
  },
  props: ["contentId"],
  watch: {
    contentId: function(v) {
      this.init(v);
    }
  },
  methods: {
    init: function(v) {
      var that = this;
      if (!v) var v = that.contentId;
      if (v) {
        that.axios
          .get(that.HOST + "/comment/getComment/" + v + "/" + 0)
          .then(res => {
            var data = res.data.message.map(function(e) {
              e.showComment = 0;
              e.sons = [];
              return e;
            });
            that.list = data;
          });
      }
    },
    addSons: function(i) {
      var obj = this.list[i];
      var that = this;
      this.axios.get(this.HOST + "/comment/getReply/" + obj.id).then(res => {
        if ((res.data.result = "success")) {
          that.list[i].showSons = 1;
          that.list[i].sons = res.data.message;
        } else that.$alert(" 网络错误 ");
      });
    },
    handleclick1: function() {
      this.isOn1 = true;
      this.isOn2 = false;
    },
    handleclick2: function() {
      this.isOn2 = true;
      this.isOn1 = false;
    },
    reply: function(v) {
      if (!v) return;
      var that = this;
      var data = new FormData();
      data.append("comments", v);
      data.append("informationId", that.contentId);
      this.axios.post(this.HOST + "/comment/addComment", data).then(res => {
        if (res.data.result == "success") {
          that.$message({
            message: "回复成功",
            type: "success"
          });
          that.init();
        } else that.$alert("请先登录！/ 网络错误");
      });
    },
    reComment: function(str, v1, v2) {
      if (!str) return;
      var that = this;
      var d = new FormData();
      d.append("comments", str);
      d.append("commentId", v1);
      this.axios.post(this.HOST + "/comment/commentReply", d).then(res => {
        if (res.data.result == "success") {
          that.$message({
            message: "回复成功",
            type: "success"
          });
          that.init();
        } else that.$alert("不可回复自己的评论 / 请先登录！");
      });
    },
    activated: function() {
      this.init(this.contentId);
    }
  }
};
</script>

<style lang="scss" scoped>
.aComment {
  padding: 5px;
  color: rgb(111, 111, 111);
  border-bottom: 1px solid rgb(211, 211, 211);
}
$blue: #127de7;
$blue_light: #5093d6;
h4 {
  font: bold 21px/30px "Microsoft YaHei", "微软雅黑", SimHe, "黑体";
  float: left;
  color: #333;
  margin: 0;
  padding-left: 2px;
}
.comment_top {
  textarea {
    box-sizing: border-box;
    resize: none;
    width: 100%;
    padding: 0;
    top: 0;
    outline: none;
    min-height: 100px;
    max-height: 100px;
    font-size: 12px;
    border-bottom: solid 1px #ccc;
    outline: none;
    display: block;
    height: 34px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42858;
    color: #555;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  }

  button {
    margin-top: 10px;
    float: right;
    margin-bottom: 0;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42858;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: #fff;
    background-color: $blue;
    border-color: $blue_light;
  }
}
.acount_right {
  float: right;
}
.comment_data {
  padding: 0 0 0 5px;
  color: #333;
  font-size: 12px;
  a {
    line-height: 40px;
    text-align: left;
    font: 12px "瀹嬩綋";
    &:hover {
      text-decoration: underline;
    }
  }
}
strong {
  font-family: Verdana;
  font-style: italic;
  font-weight: normal;
  font-size: 17px;
  color: $blue !important;
}

.comment_outline {
  & div {
    display: inline-block;
    margin: 5px 3px;
    vertical-align: top;
  }
  .comment_account {
    float: right;
    font-size: 16px;
    vertical-align: middle;
    cursor: pointer;
  }
}
.comment_exit {
  font-size: 14px;
  vertical-align: middle;
  float: right;
  color: $blue;
  &:hover {
    color: $blue;
    text-decoration: underline;
  }
}
.comment_bottom {
  margin-top: 50px;
  width: 100%;
}
.comment_show_top {
  //background:#a49f9f2b;
  display: block;
  width: 100%;
  height: 45px;
  line-height: 30px;
  ul {
    height: inherit;
    //height:30px;
    //line-height: 30px;
    width: 100%;
    height: 34px;
    line-height: 33px;
    border-bottom: 1px solid #ddd;
  }
  li {
    float: left;
    height: inherit;
    line-height: inherit;
    padding: 6px;
    //border:0.2px solid #d3d0d0;
    border-bottom: none;
    cursor: pointer;
    margin-right: 1em;
    font-family: microsoft yahei;
    font-size: 15px;
  }
}
.title-active {
  position: relative;
}
.comment_show_down {
  & {
    margin: 0;
    padding: 2px 10px;
  }
  p {
    margin: 0;
  }
}
#statement {
  color: gray;
  padding: 10px;
  border-top: solid 2px black;
  border-top: solid 2px #000;
  margin-top: 20px;
  padding: 10px 0;
  color: #999;
  line-height: 20px;
  color: #999;
  line-height: 20px;
  font-size: 12px;
  text-align: left;
}
</style>



