<template>
  <div id="user-box">
    <!-- 小部件的标题 -->
    <ul class="title no-fa">
      <li class="widget-title" v-for="(item,index) in message" :key="index" @mouseover="currentIndex = index">
        <a :class="{'title-active':currentIndex == index}">{{item.name}}</a>
      </li>
    </ul>
    <!-- 小部件显示内容 -->
    <div class="widget-content-box">
      <!--  登录框 -->
      <div v-show="currentIndex==0">
        <keep-alive>
          <user-login @getSubscriptionData='getSubscriptionData()'></user-login>
        </keep-alive>
      </div>
      <!--  纯文章 -->
      <div v-show="currentIndex!=0">
        <div class="normal-list">
          <ul>
            <li class="contentLi" v-for="(item,index) in message[currentIndex].message" v-if="index<8" :key="index">
              <a :title="item.title" class="contentNoTimeTitle" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserLogin from "@/components/common/tools/user-login";
import { getSubscribe } from "@/api/user";
export default {
  name: "userBox",
  data() {
    return {
      message: [
        {
          name: "用户中心",
          belong: "用户" //归属的小标题
        },
        {
          name: "订阅资讯",
          belong: "用户", //归属的小标题
          message: null //组件内容，对象数组
        },
        {
          name: "我的交易",
          belong: "用户", //归属的小标题
          message: null //组件内容，对象数组
        }
      ],
      currentIndex: 0,
      query: {}
    };
  },
  watch: {
    currentIndex: {
      //监听标题变更
      handler(val) {
        //更改后执行的代码
      },
      immediate: true //在watch中声明后立即执行
    }
  },

  methods: {
    //资讯文章跳转
    routerLink: function(childrenPath) {
      if (this.query.id) {
        // let path =
        //   this.message[this.currentIndex].path +
        //   this.message[this.currentIndex].path +
        //   "_" +
        //   childrenPath;
        let path = childrenPath;
        delete this.query.message.message; //删除不必要的数据
        let data = JSON.stringify(this.query); ///利用JSON传输，避免刷新后出现[Object,Object]的情况
        this.$router.push({ path: path, query: { queryMsg: data } });
      }
    },
    //获取订阅文章列表
    getSubscriptionData(i) {
      i = i || global.tabs.findIndex(item => item.name == "订阅资讯");
      getSubscribe(this.HOST)
        .then(data => {
          if (data.message)
            this.message[1].message = global.tabs[i].message = data.message;
        })
        .catch(err => {
          new Error(err);
        });
    }
  },

  mounted() {
    let i = global.tabs.findIndex(item => item.name == "订阅资讯");
    this.message[1] = global.tabs[i];
    if (!global.tabs[i].message) this.getSubscriptionData(i);
    this.message[2].message = [
      {
        title: "您购买的KR 1000 titan F已经发货",
        time: "2018-09-20"
      },
      {
        title: "您购买的KR 470-2 PA已经发货",
        time: "2018-09-20"
      },
      {
        title: "您购买的KR 3R540已经发货",
        time: "2018-09-20"
      },
      {
        title: "您购买的KR 600 R2830 F已经发货",
        time: "2018-09-20"
      },
      {
        title: "冲压模具冲压件加工冲孔需求已有人响应",
        time: "2018-09-20"
      },
      {
        title: "产品结构塑料膜具外壳设计需求已有人响应",
        time: "2018-09-20"
      },
      {
        title: "您已成功发布模具生产监理需求",
        time: "2018-09-20"
      },
      {
        title: "北广胜达已购买了您的商品，请尽快处理",
        time: "2018-09-20"
      }
    ];
  },
  components: {
    UserLogin
  }
};
</script>

<style lang="scss" scoped>
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
.title li {
  float: left;
}
.widget-title a {
  cursor: pointer;
  color: #7b7b7b;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.widget-content-box > div:first-child {
  margin-top: -3px;
}
</style>



