<template>
  <div class='association-member'>
    <div class="box1-left">
      <currentLocate></currentLocate>

      <!-- 资讯 -->
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('资讯')"></pb-list>
        </keep-alive>
      </div>

      <!-- 企业 -->
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('企业')"></pb-list>
        </keep-alive>
      </div>

      <!-- 产品 -->
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('产品')"></pb-list>
        </keep-alive>
      </div>

      <!-- 技术 -->
      <div class="box3-left">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('技术')"></pb-list>
        </keep-alive>
      </div>
    </div>

    <div class=box4-right>
      <!-- 用户 -->
      <div class="box5-right">
        <keep-alive>
          <user-box></user-box>
        </keep-alive>
      </div>
      <!-- 活动 -->
      <div class="box5-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('活动')" :widgetType="1"></pb-list>
        </keep-alive>
      </div>
      <!-- 广告（横滚） -->
      <div class="box5-right">
        <ul class="title">
          <li class="list_title">
            <a style="margin-left:15px;" class="active">广告招收栏目</a>
          </li>
        </ul>
        <div style="height:270px;width:100%;">
          <keep-alive>
            <slide2></slide2>
          </keep-alive>
        </div>
      </div>
      <!-- 人才 -->
      <div class="box5-right">
        <keep-alive>
          <pb-list :widgetMessage="tabs|FormatDate('人才')"></pb-list>
        </keep-alive>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
/*右边小框重复样式*/
@mixin litter_one_tab_box {
  @include i-box;
  @include border;
  height: 312px;
  width: 390px;
  float: right;
  margin-bottom: 12px;
}
.association-member {
  @include adv;
  height: 100%;
}
.box1-left {
  margin-top: 20px;
  width: 790px;
  float: left;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
  margin-bottom: 20px;
}
.box2-left {
  //@include i-box;
  //height: 50px;
  margin: 10px 0;
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
}
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}

/*左边小部件样式*/
.box3-left {
  @include i-box;
  min-height: 312px;
  margin-top: 12px;
  background: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
}
.box4-right {
  margin-top: 20px;
  width: 390px;
  //@include i-box;
  float: right;
}
.box4-right div {
  margin-right: 0.5px;
}
.box5-right {
  @include litter_one_tab_box;
  background: rgba(255, 255, 255, 0.95);
}
</style>

<script>
import Qs from "qs";
import currentLocate from "@/components/common/tools/currentLocate";
import Advertise from "@/components/common/tools/advertise";
import imgs from "@/common/imgs";
import PbList from "@/components/common/tools/pb-list";
import PbSearch from "@/components/common/tools/pb-search";
import UserBox from "@/components/common/tools/user-box";
import slide2 from "@/components/common/tools/slide2";
import { getMemberInfo } from "@/api/getData";
import { tabs } from "./member-tabs";

export default {
  data() {
    return {
      query: JSON.parse(this.$route.query.queryMsg), //利用JSON传输，避免刷新后出现[Object,Object]的情况
      tabs: tabs
    };
  },
  components: {
    Advertise,
    PbList,
    PbSearch,
    UserBox,
    slide2,
    currentLocate
  },
  filters: {
    FormatDate: function(value, type) {
      let data = [];
      if (type) {
        for (let i in value) {
          if (value[i].belong == type) data.push(value[i]);
        }
      }
      return data;
    }
  },
  methods: {
    init() {
      // 根据传参判断路由显示路径
      let localName = this.query.message.name;
      let localMenu = this.query.message.belong;
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == localMenu
      );
      let index2 = this.$router.options.routes[0].children[
        index
      ].children.findIndex(menu => menu.showSecondName == localName);
      global.currentPath.data = [index, index2];
      global.currentPath.path = localName;
      // 获取协会会员单位资讯相关数据
      this.getMemberInfo();
    },

    // 获取协会会员单位资讯相关数据
    getMemberInfo() {
      (async () => {
        let url = this.HOST + "/common/getMemberInfo";
        let params = Qs.stringify({
          id: this.query.id
        });
        try {
          let data = await getMemberInfo(url, params);
          Object.assign(this.query, data.message);
          // 一些特别处理
          let arr = data.message.productRecommend || [];
          this.query.productRecommend = [];
          let obj = {};
          for (let i of arr) {
            if (!obj[i.title]) {
              this.query.productRecommend.push(i);
              obj[i.title] = 1;
            }
          }
          this.query.position = (data.message.position || []).map(item => {
            item.title = item.positionName;
            return item;
          });
          // 赋值到tabs
          for (let i in this.tabs) {
            this.tabs[i].message = this.query[i] || [];
          }
        } catch (err) {
          // this.$message.error({
          //   showClose: true,
          //   message: err,
          //   duration: 2000
          // });
          console.log(err);
        }
      })();
    }
  },
  activated() {
    this.query = JSON.parse(this.$route.query.queryMsg);
    this.init();
  }
};
</script>



